import { RemoteStatsType } from "./../../models/remote-stats";
import { UserService } from "src/app/services/user.service";
import { AlertService } from "./../../services/alert.service";
import { EnvService } from "src/app/services/env.service";
import { map, catchError } from "rxjs/operators";
import { NavController, MenuController, IonRefresher } from "@ionic/angular";
import { ProviderService } from "./../../services/provider.service";
import { ProviderGroup } from "./../../models/provider-group";
import { ProviderStatus } from "./../../models/provider";
import { Component, OnInit } from "@angular/core";
import { of } from "rxjs";
import { gsap, TimelineMax } from "gsap";

import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: "app-usr-provider-groups",
  templateUrl: "./usr-provider-groups.page.html",
  styleUrls: ["./usr-provider-groups.page.scss"],
})
export class UsrProviderGroupsPage implements OnInit {
  private selectedItem: any;
  providerGorups: ProviderGroup[];
  refresher:IonRefresher;
  //statsType = RemoteStatsType;
  constructor(
    public providerService: ProviderService,
    private navController: NavController,
    private menu: MenuController,
    private env: EnvService,
    private alert: AlertService,
    private userService: UserService
  ) {
    

  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
  }
  navigateToProviders(ev, providerGroupdId: number) {
    ev.preventDefault();
    
    this.navController.navigateForward([
      "usr-providers/" + providerGroupdId,
    ]);
  }
  ionViewWillEnter() {
    this.menu.enable(true);
  }
  

  refresh(event) {
    this.refresher = event.target;
    this.providerService.providerCategoriesShared.request();
  }
  ngOnInit() {
    this.providerService.providerCategoriesShared.getUpdateNotifier(this).subscribe(()=>{
      this.refresher && this.refresher.complete();

    })
    this.providerService.providerCategoriesShared.request();
  }
  toggleCategory(catId)
  {
    const sendAnim = new TimelineMax();
    let animateContent = document.querySelector('.animate-'+catId)
    if(animateContent.clientHeight == 0)
    {
      sendAnim.to('.animate-'+catId, {
        duration: 0.3,
        height: "auto",
        ease: "back.out",
      });
    }
    else
    {
      sendAnim.to('.animate-'+catId, {
        duration: 0.3,
        height: 0,
        ease: "back.out",
      });
    }
    
  }
}
